import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Input,
  Card,
  Pagination,
  Form,
  Table,
  Space,
  Spin,
  Popconfirm,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getAddressproofs,
  addressproofEdit,
  addressproofDelete,
  isModelVisible,
  SetAddressproofsParams,
} from "../../../../store/Masters/Addressproof/addressproofSlice";
import AddAddressproof from "./addAddressproof";

function Addressproof(props) {
  useEffect(() => {
    props.getAddressproofs({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "10%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Address Proof Name",
      dataIndex: "name",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_addressproof") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.addressproofEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}
          
          {props.userAccessList?.indexOf("Masters.delete_addressproof") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.addressproofDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.addressprooflist.map((row, key) => ({
    ...row,
    index:
      (props.addressproofParams.page - 1) * props.addressproofParams.page_size +
      key +
      1,
  }));

  // function onShowSizeChange(current, pageSize) {
  //   props.getCountrys({ page_size: pageSize, page: 1 });
  // }
  function onPageChange(page, pageSize) {
    props.SetAddressproofsParams({
      ...props.addressproofParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getAddressproofs({
      ...props.addressproofParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetAddressproofsParams({ ...props.addressproofParams, search: val });
    props.getAddressproofs({ ...props.addressproofParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Address Proof Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_addressproof") !== -1 &&
          "Add Address Proof Master"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={12} className="hp-mb-16">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getAddressproofs({
                      ...props.addressproofParams,
                      page: 1,
                      search: "",
                    });
                    props.SetAddressproofsParams({
                      ...props.addressproofParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.addressproofParams?.page}
                pageSize={props.addressproofParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                // onShowSizeChange={onShowSizeChange}
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddAddressproof />
    </Row>
  );
}

function mapStateToProps({ addressproof, system }) {
  return {
    addressprooflist: addressproof.addressprooflist,
    noofpages: addressproof.noofpages,
    listCount: addressproof.listCount,
    addressproofParams: addressproof.addressproofParams,
    loading: addressproof.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getAddressproofs,
  addressproofEdit,
  addressproofDelete,
  isModelVisible,
  SetAddressproofsParams,
})(Addressproof);
